import React, { Component } from "react";
import _ from "lodash";
import styled, { ThemeProvider } from "styled-components";
import axios from "axios";
import LoadingSpinner, { LoadingContainer } from "./common/loading";
import { beUrl } from "../config";

const FilterTitle = styled.h4`
  color: ${(props) => props.theme.primaryText};
`;

const Button = styled.button`
  background-color: ${(props) => props.theme.buttonBgColor};
  color: ${(props) => props.theme.buttonFgColor};

  &.active {
    background-color: ${(props) => props.theme.activeBgColor};
    color: ${(props) => props.theme.activeFgColor};
    border-color: ${(props) => props.theme.activeBgColor};
  }

  & span {
    background-color: ${(props) => props.theme.badgeBgColor};
    color: ${(props) => props.theme.badgeFgColor};
    margin-left: 10px;
  }
`;

const ToggleWithTooltip = styled.span`
  cursor: pointer;
  margin: 0 10px;

  & .tooltiptext {
    visibility: hidden;
    opacity: 0;
    transition: 0.5s ease-in;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px;
    font-size: 0.9em;

    position: absolute;
    margin-left: 10px;
    z-index: 999;
  }

  &:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
    transition: 0.5s ease-in;
  }

  & .word {
    color: ${(props) => props.theme.badgeBgColor};
  }
`;

class Filter extends Component {
  state = {};

  async componentDidMount() {
    let { data: cars } = await axios.get(`${beUrl}/cars`);

    let brands = [];
    cars.forEach((car) => {
      const b = car.attributes.brand.value;
      if (!brands.includes(b)) {
        brands.push(b);
      }
    });
    brands = _.sortBy(brands);

    this.setState({ allBrands: brands, cars });
  }

  getUniqueBrands() {
    const { cars } = this.state;
    let brands = [];
    for (let car of cars) {
      let b = car.attributes.brand.value;
      if (!brands.includes(b)) {
        brands.push(b);
      }
    }

    brands = _.sortBy(brands);
    return brands;
  }

  countBrand(brand) {
    const { cars } = this.state;
    return cars.filter((c) => c.attributes.brand.value === brand).length;
  }

  countSold() {
    const { cars } = this.state;
    return cars.filter((c) => c.sold).length;
  }

  render() {
    const { showSold, selectedBrands } = this.props;
    const { allBrands, cars } = this.state;

    if (!allBrands || !cars) {
      return (
        <LoadingContainer>
          <LoadingSpinner></LoadingSpinner>
        </LoadingContainer>
      );
    }

    return (
      <ThemeProvider theme={this.props.theme}>
        <FilterTitle className="title1">
          Filtros{" "}
          <ToggleWithTooltip onClick={this.props.toggleVisibility}>
            <i
              className={`fas ${
                this.props.expanded ? "fa-chevron-up" : "fa-chevron-down"
              }`}
            ></i>
            <span className="tooltiptext">
              Clique para
              <span className="word">
                {this.props.expanded ? " esconder " : " mostrar "}
              </span>{" "}
              os filtros
            </span>
          </ToggleWithTooltip>
        </FilterTitle>{" "}
        {this.props.expanded ? (
          <>
            {" "}
            <div className="list-group filter1" style={{ margin: "15px 0" }}>
              <Button
                className={`list-group-item d-flex justify-content-between align-items-center ${
                  showSold ? "active" : ""
                }`}
                onClick={() => this.props.onDisableAvailableFilter()}
              >
                Todos
                <span className="badge badge-primary badge-pill">
                  {cars.length}
                </span>
              </Button>

              <Button
                className={`list-group-item d-flex justify-content-between align-items-center ${
                  !showSold ? "active" : ""
                }`}
                onClick={() => this.props.onClickAvailable()}
              >
                Disponíveis
                <span className="badge badge-primary badge-pill">
                  {cars.length - this.countSold()}
                </span>
              </Button>
            </div>
            <FilterTitle className="title2">Marcas</FilterTitle>
            <div className="list-group filter2" style={{ margin: "15px 0" }}>
              <Button
                className={`list-group-item d-flex justify-content-between align-items-center ${
                  selectedBrands.length === 0 ? "active" : ""
                }`}
                onClick={() => this.props.onClickBrand("all")}
              >
                Todas
                <span className="badge badge-primary badge-pill">
                  {cars.length}
                </span>
              </Button>
              {allBrands.map((brand) => {
                return (
                  <Button
                    key={brand}
                    className={`list-group-item d-flex justify-content-between align-items-center ${
                      selectedBrands.includes(brand) ? "active" : ""
                    }`}
                    onClick={() => this.props.onClickBrand(brand)}
                  >
                    {brand}
                    <span className="badge badge-primary badge-pill">
                      {this.countBrand(brand)}
                    </span>
                  </Button>
                );
              })}
            </div>{" "}
          </>
        ) : (
          ""
        )}
      </ThemeProvider>
    );
  }
}

export default Filter;
